
import { defineComponent, PropType } from 'vue'
import * as API from '@/API'
import * as type from '@/types'
import * as music from '@/music'
export default defineComponent({
    props: {
        playerProp: {
            type: Object as PropType<type.Player>,
            required: true,
            size: Number,
        },
        editAble: Boolean,
    },
    data() {
        return {
            player: {} as type.Player,
            settingsInput: {} as type.Settings,
            dataLoad: false,
            hardCoreMode: false,
            editProfile: false,
            volumeInput: 0,
            img: '',
            images: ['001', '002', '003', '004', '005'],
        }
    },
    mounted() {
        this.player = this.playerProp
        this.volumeInput = this.player.settings.musicVolume
        this.settingsInput = this.player.settings
        this.dataLoad = true
        if (this.editAble) {
            music.changeVolume(this.player.settings.musicVolume)
        }
    },

    computed: {
        registered() {
            let help = JSON.parse(this.player.registeredAt)
            return help
        },
        checkSettings() {
            let double = true
            if (
                [...new Set(Object.values(this.player.settings.moves))]
                    .concat([...new Set(Object.values(this.player.settings.abilitys))])
                    .filter(s => s).length < 8
            )
                double = false
            return double
        },
    },

    methods: {
        buttonSound() {
            music.ButtonSound(this.player.settings.effectVolume)
        },
        unDoChanges() {
            music.ButtonSound(this.player.settings.effectVolume)
            this.volumeInput = this.player.settings.musicVolume
            this.changeVolume(this.player.settings.musicVolume)
        },
        changeVolume(volumeInput: number) {
            music.changeVolume(volumeInput)
        },

        changeImg(id: string) {
            music.ButtonSound(this.player.settings.effectVolume)
            this.player.img = id
        },
        async safeSettings() {
            this.buttonSound()
            this.player.settings.musicVolume = this.volumeInput
            this.player.settings = this.settingsInput
            try {
                await API.addPlayer(this.player)
            } catch {
                API.logout()
            }
        },
        async toggleEdit(save: boolean) {
            music.ButtonSound(this.player.settings.effectVolume)
            this.editProfile = !this.editProfile
            if (save) {
                try {
                    await API.addPlayer(this.player)
                } catch {
                    API.logout()
                }
            }
        },
    },
})
